import request from '@/utils/request'
export function addToCartApi (goodsId, goodsNum, goodsSkuId) {
  return request.post('/cart/add', {
    goodsId,
    goodsNum,
    goodsSkuId
  })
}
export function getItemsInCart () {
  return request.get('/cart/list')
}
export function updateItemInCart (goodsId, goodsNum, goodsSkuId) {
  return request.post('/cart/update', {
    goodsId,
    goodsNum,
    goodsSkuId
  })
}
export function deleteItemInCart (cartIds) {
  return request.post('/cart/clear', {
    cartIds
  })
}
