<template>
<div class="homepage">
  <div class="top_banner">Smart Mall</div>
  <!-- <div class="searchBox">
  </div> -->
  <van-search placeholder="请输入搜索关键词" @click="onSearch" />
  <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
  <van-swipe-item v-for="item in images" :key="item.imgUrl">
    <img :src="item.imgUrl" alt="">
  </van-swipe-item>
</van-swipe>
  <van-grid :column-num="5">
    <van-grid-item v-for="item in gridItem" :key="item.imgUrl">
       <van-image :src="item.imgUrl" />
       <span v-text="item.text"></span>
    </van-grid-item>
  </van-grid>
    <img :src="toCategory.imgUrl" :title="toCategory.imgName" width="100%">
    <van-card v-for="item in goodsList" :key="item.goods_id"
      :num="item.goods_sales"
      :price="item.goods_price_max"
      :title="item.goods_name"
      :thumb="item.goods_image"
      :origin-price="item.line_price_max"
      @click="handleClick(item.goods_id)"
    />
</div>

</template>

<script>
import { getHomePageData } from '@/api/login'
// import { Swipe, SwipeItem } from 'vant'

export default {
  name: 'homeIndex',
  async created () {
    const res = await getHomePageData()
    // console.log(res)
    this.images = res.data.pageData.items[1].data
    this.gridItem = res.data.pageData.items[3].data
    // console.log(this.images)
    this.toCategory = res.data.pageData.items[4].data[0]
    // console.log(this.toCategory)
    this.goodsList = res.data.pageData.items[6].data
  },
  data () {
    return {
      images: [],
      gridItem: [],
      toCategory: {},
      goodsList: []
    }
  },
  methods: {
    onSearch () {
      this.$router.push('/search')
    },
    handleClick (goodsId) {
      // console.log(goodsId)
      this.$router.push(`/product?goodsId=${goodsId}`)
    }
  }
}
</script>
<style scoped>
  .top_banner{
    background-color: #e53737;
    text-align: center;
    width: 100%;
    height: 30px;
    color: white;
    font-size: 15px;
    line-height: 30px;
  }
  .searchBox{
    border: 1px solid black;
    height: 20px;
    margin: 0 auto;
  }
 .my-swipe .van-swipe-item {
    color: #fff;
    font-size: 20px;
    /* line-height: 150px; */
    /* text-align: center; */
    /* background-color: #39a9ed; */
    /* background-image: ; */
  }
 .my-swipe .van-swipe-item img{
  width: 100%;
 }
 .van-grid .van-grid-item span{
  font-size: 10px;
 }
</style>
