import Vue from 'vue'
import VueRouter from 'vue-router'

import layout from '@/views/layout'
import home from '@/views/layout/home'
import category from '@/views/layout/category'
import cart from '@/views/layout/cart'
import account from '@/views/layout/account'
// lazy load按需加载，提升性能
const login = () => import('@/views/login')
const orders = () => import('@/views/order/orders')
const pay = () => import('@/views/order/pay')
const submit = () => import('@/views/order/sumbit')
const product = () => import('@/views/product')
const search = () => import('@/views/search')
const searchList = () => import('@/views/search/list')
const reviewPage = () => import('@/views/reviewPage')
const store = () => import('@/store')
const addresses = () => import('@/views/layout/account/address')
const addressDetail = () => import('@/views/layout/account/address/addressDetail')
const orderDetail = () => import('@/views/order/orderDetail')
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: layout,
    redirect: '/home',
    children: [
      { path: '/home', component: home },
      { path: '/category', component: category },
      { path: '/cart', component: cart },
      { path: '/account', component: account }
    ]
  },
  { path: '/login', component: login },
  { path: '/orders', component: orders },
  { path: '/pay', component: pay },
  { path: '/submit', component: submit },
  { path: '/product', component: product },
  { path: '/reviewDetail', component: reviewPage },
  { path: '/search', component: search },
  { path: '/searchList', component: searchList },
  { path: '/addresses', component: addresses },
  { path: '/addressDetail', component: addressDetail },
  { path: '/orderDetail', component: orderDetail }

]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})
const authUrls = ['/pay', '/order']
router.beforeEach((to, from, next) => {
  // console.log(to, from, next)
  if (!authUrls.includes(to.path)) {
    next()
    return
  }
  if (store.state.user.userInfo.token === '') {
    // console.log(store.state.user.userInfo.token)
    next({
      path: '/login'
    })
    return
  }
  next()
})
export default router
