<template>
  <div class="user-center">
     <div class="unlogin" v-if="currentState===false">
      <van-empty image="search" description="请先登录" />
      <my-button-ver1 class="theButton" @button-clicked="$router.push('/login')">
        <template v-slot:content>
          去登陆
        </template>
      </my-button-ver1>
    </div>
   <div class='wrapper' v-else>
     <user-info :userInfo="userInfo"/>
    <div class="user-assets">
      <div class="asset-item" @click="showModal('balance')">
        余额：¥{{balance}}
      </div>
      <div class="asset-item" @click="showModal('points')">
        积分：{{points}}
      </div>
      <div class="asset-item" @click="showModal('coupons')">
        优惠券：{{coupons}}张
      </div>
    </div>
    <div class="management">
      <router-link to="/orders" class="management-item">
        订单管理
      </router-link>
      <router-link to="/addresses" class="management-item">
        地址管理
      </router-link>
      <button @click="logout" class="logout-button">
        退出登录
      </button>
    </div>

    <modal-popup v-if="showPopup" @close="closeModal">
      <template v-if="popupType === 'balance'">
        <h3>余额详情</h3>
        <p>当前余额：¥{{balance}}</p>
      </template>
      <template v-if="popupType === 'points'">
        <h3>积分详情</h3>
        <p>当前积分：{{points}}</p>
      </template>
      <template v-if="popupType === 'coupons'">
        <h3>优惠券详情</h3>
        <p>当前优惠券：{{coupons}}张</p>
      </template>
    </modal-popup>
    </div>
  </div>
</template>

<script>
import UserInfo from '@/components/userInfo'
import ModalPopup from '@/components/modalPopup'
import { Dialog, Notify } from 'vant'
import { mapMutations } from 'vuex'
import { getPointsApi, getUserInfoApi } from '@/api/account'
import myButtonVer1 from '@/components/myButtonVer1.vue'
export default {
  name: 'accountIndex',
  components: {
    UserInfo,
    ModalPopup,
    myButtonVer1
  },
  data () {
    return {
      showPopup: false,
      popupType: '',
      balance: 0,
      coupons: 0,
      points: 0,
      userInfo: {}
    }
  },
  async beforeCreate () {
    if (this.currentState === false) {
      return
    }
    const res = await getPointsApi()
    // console.log('points:', res)
    this.balance = res.data.assets.balance
    this.points = res.data.assets.points
    this.coupons = res.data.assets.coupon
    const { data: { userInfo } } = await getUserInfoApi()
    // console.log(userInfo)
    this.userInfo = userInfo
    this.userInfo.avatarUrl = 'https://tse2-mm.cn.bing.net/th/id/OIP-C.jHUH4s7TQ48X_B-1iozuJgHaHa?w=176&h=180&c=7&r=0&o=5&dpr=1.3&pid=1.7'
  },
  methods: {
    showModal (type) {
      this.popupType = type
      this.showPopup = true
    },
    closeModal () {
      this.showPopup = false
      this.popupType = ''
    },
    logout () {
      Dialog.confirm({
        title: '退出登录',
        message: '此操作不可撤销'
      })
        .then(() => {
          // on confirm
          this.clearUserInfo()
          this.clearAllInCart()
          Notify('已退出登录')
          window.location.reload()
        })
        .catch(() => {
          // on cancel

        })
    },
    ...mapMutations('user', ['clearUserInfo']),
    ...mapMutations('cart', ['clearAllInCart'])
  },
  computed: {
    currentState () {
      // console.log('$store.getters.currentState', this.$store.getters['user/currentState'])
      return this.$userLocalStorage.getLocalState()
    }
  }
}
</script>

<style scoped>
.unlogin .theButton{
  text-align: center;
}
.user-center {
  padding: 20px;
}

.user-assets {
  margin: 20px 0;
  /* display: flex; */
  /* justify-content: space-between; */
}

.asset-item {
  /* flex-grow: 1; */
  padding: 10px;
  margin: 0 5px;
  background-color: #f5f5f5;
  text-align: left;
  cursor: pointer;
  border-radius: 8px;
}

.management {
  margin-top: 20px;
}

.management-item {
  display: block;
  padding: 10px;
  background-color: #f5f5f5;
  margin-bottom: 10px;
  text-align: center;
  text-decoration: none;
  color: #333;
}

.logout-button {
  display: block;
  width: 100%;
  padding: 10px;
  background-color: #ff4d4f;
  color: white;
  border: none;
  text-align: center;
  cursor: pointer;
}

.logout-button:hover {
  background-color: #ff7875;
}
</style>
