<template>
<div class="category">
    <van-nav-bar title="分类" fixed />
    <van-search
      readonly
      shape="round"
      background="#f1f1f2"
      placeholder="请输入搜索关键词"
      @click="$router.push('/search')"
    />
<van-sidebar class="sideBar" v-model="activeKey" @change="onChange" ref="theSidebar">
  <van-sidebar-item v-for="item in categoryList" :key="item.category_id" :title="item.name"/>
</van-sidebar>
  <div class="content">
    <van-grid :column-num="3">
      <van-grid-item v-for="item in itemList" :key="item.image_id" :text="item.name"  @click="handleClick(item.goods_id)">
        <img :src="item.image.external_url" alt="">
        <span v-text="item.name"></span>
      </van-grid-item>
    </van-grid>
    <van-empty description="空空如也" v-if="itemList.length==0" />
  </div>
</div>
</template>

<script>
import { getGoodsCategory } from '@/api/searchGoods'
export default {
  name: 'categroyIndex',
  data () {
    return {
      categoryList: [],
      itemList: [],
      activeKey: 0
    }
  },
  methods: {
    onChange (index) {
      // console.log(this.categoryList[index])
      if (typeof this.categoryList[index].children !== 'undefined') {
        // console.log(index)
        this.itemList = this.categoryList[index].children
      } else {
        this.itemList = []
      }
    },
    handleClick (goodsId) {
      // if (this.$refs.theSidebar.index === 0) {
      //   return
      // }
      // this.$router.push(`/product?goodsId=${goodsId}`)
    }
  },
  async created () {
    const res = await getGoodsCategory()
    // console.log(res)
    this.categoryList = res.data.list
    this.itemList = this.categoryList[0].children
  }
}
</script>

<style scoped>
.sideBar{
  /* border: 1px solid black; */
  /* margin-top: 5px; */
  width: 30%;
  display: inline-block;
  /* overflow: scroll; */
  height: 80%;
  /* position: relative; */
  position: fixed;
  /* bottom: 100px; */
  top: 90px;
}
.van-search{
  margin-top: 30px;
}
.content{
  /* border: 1px solid black; */
  width: 65%;
  position: absolute;
  left: 100px;
  right: 0;
  top: 80px;
}
.van-grid{
  margin-bottom: 50px;
}
.van-grid-item img{
  width: 100%;
}
.van-grid-item span{
  font-size: 12px;
  overflow: hidden;
  text-overflow: ellipsis;
  overflow-wrap: normal;
}
</style>
