<template>
  <div class="cart">
    <h3 class="top-banner">购物车</h3>
    <div class="unlogin" v-if="currentState===false">
      <van-empty image="search" description="请先登录" />
      <my-button-ver1 class="theButton" @button-clicked="$router.push('/login')">
        <template v-slot:content>
          去登陆
        </template>
      </my-button-ver1>
    </div>
    <van-empty image="search" description="空空如也" v-else v-show="cartTotal===0" />
    <div class="list" v-show="cartTotal!==0">
      <div class="edit">
        <div class="left">共<span>{{cartTotal}}</span>件</div>
        <div class="right" @click="handleClickEidt" ><van-icon name="edit" ref='edit'/></div>
      </div>
      <div class="item" v-for="item in itemList" :key="item.goods_id">
        <van-checkbox v-model="item.isChecked" checked-color="rgb(221 32 23)"></van-checkbox>
        <a :href="'http://localhost:8080/#/product?goodsId='+item.goods.goods_id">
          <img :src="item.goods.goods_image" :title="item.goods.goods_name">
        </a>
        <div class="product">
          <div class="des">{{item.goods.goods_name}}</div>
          <span class="price">￥{{item.goods.skuInfo.goods_price}}</span>
          <count-box :value="item.goods_num" @input="value=>handelInputUpdate(item.goods_id,value,item.goods_sku_id)" ></count-box>
        </div>
      </div>
      <div class="fill-box"></div>
      <div class="bottom-banner">
        <van-checkbox v-model="checkAll" checked-color="rgb(221 32 23)">全选</van-checkbox>
        合计：￥<span style="color:red;font-weight: bold; padding-right: 20px">{{totalPrice}}</span>
        <van-button type="primary" :round="true" @click="handelClickBtn"><span ref="btn"></span>({{selectedCount}})</van-button>
      </div>
    </div>
  </div>
</template>

<script>
import myButtonVer1 from '@/components/myButtonVer1.vue'
import countBox from '@/components/countBox.vue'
import { mapState, mapGetters, mapMutations } from 'vuex'
import { Notify } from 'vant'
// import { getItemsInCart } from '@/api/cart'
export default {
  name: 'cartIndex',
  data () {
    return {
      isChecked: false,
      value: 12,
      reactiveText: {
        status: 0,
        list: [
          { edit: '编辑', btn: '结算' },
          { edit: '取消编辑', btn: '删除' }
        ]
      }
    }
  },
  components: {
    myButtonVer1,
    countBox
  },
  computed: {
    currentState () {
      // console.log('$store.getters.currentState', this.$store.getters['user/currentState'])
      return this.$userLocalStorage.getLocalState()
    },
    checkAll: {
      get () {
        return this.itemList.every(element => element.isChecked)
      },
      set (value) {
        if (value === true) {
          this.checkAllItems()
        } else {
          // console.log(value)
          this.cancelCheckAllItems()
        }
      }
    },
    ...mapState('cart', ['itemList', 'cartTotal']),
    ...mapGetters('cart', ['selectedCount', 'totalPrice', 'totalVarieties', 'totalCount', 'list', 'selectedItems'])
  },
  async created () {
    if (this.currentState) {
      // const res = await getItemsInCart()
      // console.log('cartList:', res)

      await this.$store.dispatch('cart/getItemList')
      // console.log('vuex:', this.$store.state.cart.itemList)
      this.reactiveText.status = 0
      this.$refs.edit.textContent = this.reactiveText.list[0].edit
      this.$refs.btn.textContent = this.reactiveText.list[0].btn
    }
  },
  methods: {
    // event handelers
    handleClickEidt () {
      // console.log('clicked eidt', this.$refs.edit)
      if (this.reactiveText.status === 0) {
        this.reactiveText.status = 1
        this.$refs.edit.textContent = this.reactiveText.list[1].edit
        this.$refs.btn.textContent = this.reactiveText.list[1].btn
      } else {
        this.reactiveText.status = 0
        this.$refs.edit.textContent = this.reactiveText.list[0].edit
        this.$refs.btn.textContent = this.reactiveText.list[0].btn
      }
    },
    async handelInputUpdate (goodsId, goodsNum, goodsSkuId) {
      // console.log(value)
      // console.log('prams:', { goodsId, goodsNum, goodsSkuId })
      await this.$store.dispatch('cart/updateItem', { goodsId, goodsNum, goodsSkuId })
    },
    ...mapMutations('cart', ['checkAllItems', 'cancelCheckAllItems']),
    handelClickBtn () {
      if (this.reactiveText.status === 0) {
        this.checkItems()
      } else {
        this.deleteItems()
      }
    },
    async deleteItems () {
      if (this.selectedCount === 0) {
        return
      }
      await this.$store.dispatch('cart/deleteItem')
    },
    checkItems () {
      if (this.selectedCount === 0) {
        Notify('请选择商品')
        return
      }
      this.$router.push('/submit')
    }
  }
}
</script>

<style scoped>
  .theButton{
    text-align: center;
  }
  .top-banner{
    text-align: center;
  }
  .list {
    background-color: #eaedef;

  }
  .list .edit{
    position: relative;
    height: 24px;
  }
  .list .edit .left{
    position: absolute;
    left: 0;
  }
  .list .edit .left span{
    color: red;
    font-weight: bold;
  }
  .list .edit .right{
    position: absolute;
    right: 0;
  }
  .list .item{
    /* border: 1px solid black; */
    background-color: #fff;
    height: 100px;
    width: 90%;
    margin: 5px auto;
    position: relative;
  }
  .list .fill-box{
    height: 110px;
  }
  .list .item .van-checkbox{
    position: absolute;
    top: 30px;
    display: inline-block;
  }
  .list .item img{
    /* border: 1px solid black; */
    position: absolute;
    left: 30px;
    width: 100px;
  }
  .list .item .product{
    /* border: 1px solid rgb(224, 162, 162); */
    position: absolute;
    right: 0;
    left: 150px;
    height: 100%;
  }
  .list .item .product .des {
    font-size: 12px;
    height: 30px;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .list .item .product .price{
    color: red;
    font-weight: bold;
    font-size: 16px;
    position:absolute;
    bottom: 5px;
    /* left: 150px; */
  }
  .list .item .product .count-box{
    /* background-color: #e18a8a; */
    position: absolute;
    right: 0;
  }
  .list .bottom-banner{
    background-color: #fbfbfb;
    position: fixed;
    bottom: 50px;
    text-align: right;
    width: 100%;
    border-bottom: 1px solid #ccc;
  }
  .list .bottom-banner .van-checkbox{
    float: left;
    padding-top: 10px;
  }

</style>
