const TheKey = 'user_info'
export default {
  setStorage: function (newValue) {
    localStorage.setItem(TheKey, JSON.stringify(newValue))
  },
  getStorage: function () {
    const result = JSON.parse(localStorage.getItem(TheKey))
    return result || { token: '', userID: '' }
  },
  getLocalState: function () {
    const result = JSON.parse(localStorage.getItem(TheKey))
    if (result) {
      return true
    }
    return false
  },
  removeStorage: function () {
    localStorage.removeItem(TheKey)
  }
}
