import { getItemsInCart, updateItemInCart, deleteItemInCart } from '@/api/cart'
const state = {
  itemList: [],
  cartTotal: 0
}
const getters = {
  selectedCount (state) {
    return state.itemList.reduce((sum, item) => {
      if (item.isChecked === true) {
        sum += item.goods_num
      }
      return sum
    }, 0)
  },
  totalPrice (state) {
    return state.itemList.reduce((sum, item) => {
      if (item.isChecked === true) {
        sum += item.goods_num * item.goods.skuInfo.goods_price
      }
      return sum
    }, 0)
  },
  cartIds (state) {
    const result = []
    state.itemList.forEach(element => {
      if (element.isChecked === true) {
        result.push(element.id)
      }
    })
    return result
  },
  // this method returns all the selected ids despite the amount
  cartIdsAll (state) {
    const result = []
    state.itemList.forEach(element => {
      if (element.isChecked === true) {
        for (let i = 0; i < element.goods_num; i++) {
          result.push(element.id)
        }
      }
    })
    return result
  },
  selectedItems (state) {
    const result = []
    state.itemList.forEach(element => {
      if (element.isChecked) {
        result.push(element)
      }
    })
    return result
  }
}
const mutations = {
  setItemList (state, newList) {
    state.itemList = newList
  },
  setCartTotal (state, value) {
    state.cartTotal = value
  },
  checkAllItems (state) {
    state.itemList.forEach(element => {
      element.isChecked = true
    })
  },
  cancelCheckAllItems (state) {
    state.itemList.forEach(element => {
      element.isChecked = false
    })
  },
  updateItemNum (state, { goodsNum, goodsId }) {
    state.itemList.forEach(element => {
      if (element.goods_id === goodsId) {
        element.goods_num = goodsNum
        return true
      }
    })
    return false
  },
  deleteItems_Mutation (state) {
    for (let i = state.itemList.length - 1; i >= 0; i--) {
      if (state.itemList[i].isChecked === true) {
        state.itemList.splice(i, 1)
      }
    }
  },
  clearAllInCart (state) {
    state.itemList = []
    state.cartTotal = 0
  }
}
const actions = {
  async getItemList (context) {
    const { data } = await getItemsInCart()
    // console.log('res in actions:', data)
    data.list.forEach(element => {
      element.isChecked = false
    })
    context.commit('setItemList', data.list)
    context.commit('setCartTotal', data.cartTotal)
  },
  async updateItem (context, obj) {
    const { goodsId, goodsNum, goodsSkuId } = obj
    const res = await updateItemInCart(goodsId, goodsNum, goodsSkuId)
    // console.log('update', res)
    context.commit('setCartTotal', res.data.cartTotal)
    context.commit('updateItemNum', obj)
  },
  async deleteItem (context) {
    // console.log('cartIds:', context.getters.cartIds)
    const res = await deleteItemInCart(context.getters.cartIds)
    // console.log('delete:', res)
    context.commit('deleteItems_Mutation')
    context.commit('setCartTotal', res.data.cartTotal)
    // console.log('after delete:', context.state.itemList)
  }
}
export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
