import request from '@/utils/request'
export function queryGoods (goodsName) {
  return request.get('/goods/list', {
    params: {
      goodsName
    }
  })
}
export function queryGoodsSort (goodsName, value) {
  return request.get('/goods/list', {
    params: {
      goodsName,
      sortType: value ? 'price' : 'sales'
    }
  })
}
export function getGoodsCategory () {
  return request.get('/category/list')
}
