import userStorage from '@/utils/storage'
const state = {
  userInfo: userStorage.getStorage()
}
const getters = {
}
const mutations = {
  setUserInfo (state, newUser) {
    state.userInfo = newUser
    userStorage.setStorage(state.userInfo)
  },
  clearUserInfo (state) {
    state.userInfo = {}
    userStorage.removeStorage()
  }
}
const actions = {

}
export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
